body {
  font-family: Arial, Helvetica, sans-serif;
}

#title {
  text-align: center;
}

#schedule {
  font-size: 10px;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-auto-rows: 90px;
  border: 1px solid #444;
}

.case {
  display: grid;
  border: 1px solid #444;
}

.subCase {
  display: inline-block;
  position: relative;
  overflow: hidden;
}

.subCase>div>span {
  text-align: center;
  display: inline-block;
  width: 100%;
  /*margin-top: 2px;
  margin-bottom: 2px;
  */
}

.subCase>div{
  position: relative;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}

#filterBox {
  padding: 20px;
}

#filterBox > div{
  margin-left: 1em;
  margin-right: 2em;
  display: inline-block;
  float: left;
}

#filterBox::after{
  content: "";
  display: block;
  clear: both;
}

#filterBox > div > label {
  display: inline-block;
  width: 75px;
}

#filterBox > div > select {
  width: 75px;
}

input[type="number"] {
  text-align: center;
}

#weekLabel {
  position: relative;
  left: 1em;
  bottom: 6px;
}

#groups>div{
  margin: 0 1em 0 1em;
  display: inline-block;
}

.groupChooser {
  display: grid;
  grid-template-columns: repeat(8, 1fr);
  border: 1px solid black;
  border-radius: 4px;
  overflow: hidden;
  width: 300px;
}

.groupChooser > div {
  padding: .5em 0 .5em 0;
  cursor: pointer;
  text-align: center;
  border: 1px solid black;
  background-color: #AAA;
  font-weight: bold;
  transition: background-color .2s
}

.groupChooser>div:hover {
  background-color: #DDD;
}

.groupChooser>.selected {
  border: 1px solid #1b5e20;
  background-color: #66bb6a;
}

.groupChooser>.selected:hover {
  background-color: #c8e6c9;
}

@media screen and (max-width: 383px) {
  #filterBox{
    padding: 0 0 20px 0 !important;
  }
  #groups{
    margin-left: 0em !important;
    margin-right: 0em !important;
    width: 98%;
  }
  #groups>div{
    width: 100%;
    margin-left: 0em !important;
    margin-right: 0em !important;
  }
  .groupChooser {
    width: 100%;
  }
}
